const Accordion = class {
    constructor(el) {
        this.accordions = el;

        this._init_events();
    }

    _init_events() {
        this.accordions.on('click', this.accordion_click.bind(this));
    }

    accordion_click(e) {
        console.log(e.currentTarget);
    }
};

new Accordion($('.accordion__title'));